@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans.ttf") format("truetype");
}
@font-face {
  font-family: "Rubik";
  src: url("../fonts/Rubik.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto.ttf") format("truetype");
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  font-family: sans-serif;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
  object-fit: contain;
}

button,
input,
textarea,
select {
  font: inherit;
  border: none;
  outline: none;
  background: transparent;
}

button {
  cursor: pointer;
}

body {
  font-family: "Open Sans", sans-serif;
  color: #575c74;
  font-weight: 400;
  background-color: #fff;
  position: relative;
  min-height: 100vh;
  overflow-x: hidden;
}
body::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  max-width: 927px;
  width: 100%;
  height: 891px;
  background-image: url("../images/backround-img-1x.webp");
  background-size: cover;
  background-position: center;
  opacity: 0.15;
  z-index: 1;
  border-radius: 0 0 0 150px;
}
@media screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx), screen and (-webkit-min-device-pixel-ratio: 2) {
  body::before {
    background-image: url("../images/backround-img-2x.webp");
  }
}
body::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1200px;
  background-color: #f5fcfa;
  top: 0;
  left: 0;
  z-index: 0;
}
body .shape-2 {
  position: absolute;
  width: 100%;
  height: 2491px;
  top: 969px;
  left: 0;
  background-image: url("../images/backround.webp");
  background-size: cover;
  background-position: center;
  z-index: 1;
}
body .shape-3 {
  position: absolute;
  width: 100%;
  height: 1614px;
  border-radius: 0 0 0 150px;
  background-color: #f7fdfb;
  left: 0;
  top: 2969px;
  z-index: 1;
}

.main-content {
  position: relative;
  z-index: 2;
}

