.mobile-menu {
  position: fixed;
  z-index: 22;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  background-color: #e5f1ee;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1), visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}
.mobile-menu .mobile-menu-container {
  position: relative;
  padding-top: 72px;
  padding-bottom: 40px;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.mobile-menu .mobile-menu-container .mobile-menu-close {
  position: absolute;
  top: 24px;
  right: 24px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 1px solid rgba(46, 47, 66, 0.1);
  display: flex;
  padding: 0;
  align-items: center;
  justify-content: center;
}
.mobile-menu .mobile-menu-container nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
  flex-direction: column;
}
.mobile-menu .mobile-menu-container nav ul li {
  font-size: 16px;
  line-height: 188%;
}
.mobile-menu .mobile-menu-container nav ul li:first-child {
  color: #0c122d;
}
.mobile-menu .mobile-menu-container nav ul li button {
  border-radius: 8px;
  width: 123px;
  height: 47px;
  background-color: #1b5fc1;
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 214%;
  text-align: center;
  color: #fff;
}

